import "./CryptoStatistics.scss"

const Cryptocurrencies = [
    {
        name:"Internet Computer",
        price:"3.53",
        discount:"1.85"
    },
    {
        name:"Cosmos Hub",
        price:"7.20",
        discount:"3.64"
    },
    {
        name:"Filecoin",
        price:"3.33",
        discount:"3.55"
    },
    {
        name:"Lido DAO",
        price:"1.64",
        discount:"3.37"
    },
    {
        name:"Cornos",
        price:"0.051407",
        discount:"0.56"
    },
    {
        name:"Aptos",
        price:"5.81",
        discount:"4.44"
    },
    {
        name:"VeChain",
        price:"0.015",
        discount:"3.60"
    },
    {
        name:"VeChain",
        price:"0.015",
        discount:"3.60"
    },
    {
        name:"VeChain",
        price:"0.015",
        discount:"3.60"
    },
]



function CryptoStatistics() {
    return (
        <div className="crypto-statistics">
            <div className="crypto-statistics__running-line">
                {Cryptocurrencies.map(Cryptocurrency =>(
                    
                    <article className="running-line__element" key={Cryptocurrencies.indexOf(Cryptocurrency)}>
                        <h3 className="running-line__name">{Cryptocurrency.name}</h3>
                        <p className="running-line__price">${Cryptocurrency.price}</p>
                        <span className="running-line__discount">-{Cryptocurrency.discount}%</span>
                    </article>
                    
                ))}  
            </div>
        </div>
    )
}

export default CryptoStatistics