import React, { useState } from "react";
import "./ModalWindow.scss"
import Registration from "../registration/Registration";
import Login from "../login/Login"


const ModalWindow = ({ isActive }) => {

    const [isNewUser, setIsNewUser] = useState(false)

    const isChangeComponent = (e) => {
        if (e.target.hasAttribute("data-login")) { setIsNewUser(false) }
        else { setIsNewUser(true) }
        e.target.parentNode.querySelectorAll('button').forEach(el => { el.classList.remove('is-active-auth'); }); // get elements through the parent element and remove their class "isActiveAuth"
        e.target.classList.add("is-active-auth")
        e.stopPropagation();
    }

    return (
        <>
            <div className="overlay" onClick={() => { isActive(false) }}></div>
            <div className="modal-window ">
                <div className="modal-window-section">
                    <div className="modal-window-section__top">
                        <div className="modal-window-section__top-title">
                            Personal account
                        </div>
                        <div className="modal-window-section__top-select">
                            <button onClick={isChangeComponent} data-login className="modal-window-section__top-select__btn is-active-auth">Sign In</button>
                            <hr className="modal-window-section__top-select__hr" />
                            <button onClick={isChangeComponent} className="modal-window-section__top-select__btn">Sign Up</button>
                        </div>
                    </div>
                    {isNewUser ? <Registration isActive={isActive}/> : <Login isActive={isActive}/>}
                    <div className="modal-window-section__bottom">
                        <span>
                            Google
                        </span>
                    </div>
                </div>
            </div>
        </>

    )
}

export default ModalWindow;