import React from "react";
import "./PriceFilter.scss"


const setDefaultMoney = (event) => {
    if (event.target.value === "") {
        event.target.value = event.target.defaultValue
    }
}

const PriceFilter = () => {
    return (
        <div className="filter-box">
            <div className="filter-title">
            On Price
            </div>
            <div className="filter-actions">
            <div className="filter-price-inputs">
                    <div className="filter-price-input">
                        <input type="text" className="price-input" defaultValue={0} onBlur={setDefaultMoney} />
                    </div>
                    <div>
                        
                    </div>
                    <div className="filter-price-input">
                        <input type="text" className="price-input" defaultValue={30000} onBlur={setDefaultMoney} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PriceFilter;