import React from 'react';

import { HashRouter, BrowserRouter, Routes, Route, useLocation  } from 'react-router-dom'
import { BreadcrumbsProvider, Breadcrumbs } from 'react-breadcrumbs-dynamic'

import Header from './components/header/Header'
import Footer from './components/footer/Footer';
import Home from "./pages/home/Home"

import Products from './pages/products/Products';
import Product from './pages/product/Product';
import EmptyPage from './pages/EmptyPage/EmptyPage';
import Profile from './pages/profile/Profile';
import ProfileCoupons from './pages/profile/profile-coupons/ProfileCoupons';
import ProfileOffice from './pages/profile/profile-office/ProfileOffice';
import ProfileWishlist from './pages/profile/profile-wishlist/ProfileWishlist';
import ProfileOrders from './pages/profile/profile-orders/ProfileOrders';
import ProfileSafety from './pages/profile/profile-safety/ProfileSafety';
import ProfileDeliveryAddress from './pages/profile/profile-delivery-address/ProfileDeliveryAddress';
import ProfileData from './pages/profile/profile-data/ProfileData';
import ProfileNotification from './pages/profile/profile-notification/ProfileNotification';
import Blog from './pages/blog/Blog';
import PostPage from './pages/blog-post-page/PostPage';

function ScrollToTop() {
    const { pathname } = useLocation();
  
    React.useEffect(() => {
      window.scrollTo(0, 0);
    }, [pathname]);
  
    return null
  }

function App(){
    return(
        <BrowserRouter>
            <BreadcrumbsProvider>
            <>
                <ScrollToTop />      
                <Header/>
                <Breadcrumbs/>
                <Routes>
                            <Route path="/" element={<Home/>} />
                            {/* <Route path="/about-us" element={<About/>}/> */}
                            <Route path="/products" element={<Products/>}/>
                            <Route path="/products/:id" element={<Product/>}/>
                            <Route path="/personal-profile/" element={<Profile/>}>
                                <Route index element={<ProfileOffice/>}/>
                                <Route path="coupons" element={<ProfileCoupons/>}/>
                                <Route path="wishlist" element={<ProfileWishlist/>}/>
                                <Route path="orders" element={<ProfileOrders/>}/>
                                <Route path="personal-data" element={<ProfileData/>}/>
                                <Route path="address" element={<ProfileDeliveryAddress/>}/>
                                <Route path="safety" element={<ProfileSafety/>}/>
                                <Route path="notification" element={<ProfileNotification/>}/>
                            </Route>
                            <Route path="/blog" element={<Blog/>}/>
                            <Route path="/blog/:id" element={<PostPage/>}/>
                            <Route path="/*" element={<EmptyPage/>}/>
                </Routes>
                <Footer/>
            </>
            </BreadcrumbsProvider>
       </BrowserRouter>
    );
}

export default App;