    const [isPaymentTab, setIsPaymentTab] = useState(true)

    const changeCount = (e) => {
        if (e.target.hasAttribute("data-plus")) {
            setCount(count + 1)
        }
        else {
            if (count >= 1) {
                setCount(count - 1)
            }
        }
        e.stopPropagation();
    }

    const changeTab = (e) => {

        const buttons = document.querySelectorAll("#Product-Tab")
        buttons.forEach(button => (
            button.classList.remove("product-active-tabs")
        ))

        if (e.target.dataset.name === "payment-delivery") {
            setIsPaymentTab(true)
            e.target.classList.add("product-active-tabs")
        } else {
            setIsPaymentTab(false)
            e.target.classList.add("product-active-tabs")
        }

        e.stopPropagation();
    }

