import React, { useState } from "react";
import "./ContactForm.scss"

function ContactForm() {

    const [isSendMessage, setIsSendMessage] = useState(false)

    return (
        <>
            <section className="send-message-section">
                <div className="send-message-section__content">
                    <div className="send-message-section__content-title">
                        <h1 className="content-title__h1 section-h1">GET IN TOUCH NOW</h1>
                        <p className="content-title__p section-p">Questions about our service?</p>
                        <button className="content-title__button" onClick={() => {setIsSendMessage(!isSendMessage)}}>
                            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <g clipPath="url(#clip0_580_1332)">
                                    <path d="M22.522 5.10278H1.52197V20.3728H22.522V5.10278Z" stroke="#4F5665" strokeWidth="1.5" strokeMiterlimit="10" />
                                    <path d="M1.52197 5.10278L12.022 15.6028L22.522 5.10278" stroke="#4F5665" strokeWidth="1.5" strokeMiterlimit="10" />
                                </g>
                                <defs>
                                    <clipPath id="clip0_580_1332">
                                        <rect width="24" height="24" fill="white" transform="translate(0.0219727 0.742676)" />
                                    </clipPath>
                                </defs>
                            </svg>
                            {
                                isSendMessage &&
                                <span>Send Message</span>
                            }
                        </button>
                    </div>
                    {
                        isSendMessage &&
                        <div className="send-message-section__content-form">
                            <div className="content-form__inputs">
                                <input placeholder="Name" />
                                <input placeholder="Email" />
                                <input placeholder="Company Name" />
                                <input placeholder="Contact Number" />
                                <textarea placeholder="Message" />
                            </div>
                            <button className="content-form__btn section-btn">
                                Submit
                            </button>
                        </div>
                    }

                </div>
            </section>
        </>
    )
}

export default ContactForm