import React from "react";

import "./Blog.scss"
import BlogPost from "../../components/blog-post/BlogPost";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";

const posts = Array.from({length: 13}, (_, index) => index + 1);



const Blog = () => {

    return (
        <>
       <BreadcrumbsItem to="/"><b>Blog</b></BreadcrumbsItem>
        <div className="blog-page">
            <div className="blog-posts">
                {posts.map(e => (
                    <BlogPost id={`post${e}`} key={e}/>
                ))}
            </div>
            <div className="blog-buttons">
                <div className="blog-buttons__show-more">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0_1513_3332)">
                            <path d="M22.5196 12C22.525 14.2383 21.8164 16.4201 20.4966 18.228C19.1769 20.0359 17.3149 21.3757 15.1814 22.0527C13.0479 22.7296 10.754 22.7085 8.63336 21.9922C6.5127 21.276 4.67577 19.902 3.38965 18.07" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <path d="M3.38965 23.5V17.76H9.12965" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <path d="M2.28027 7.99995C2.9454 6.39312 3.99886 4.97644 5.34621 3.87691C6.69357 2.77739 8.29271 2.02938 10.0003 1.69995" stroke="black" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                            <path d="M16 2.30005C17.6071 2.96219 19.0253 4.01187 20.1281 5.35544C21.2309 6.69901 21.9839 8.29467 22.32 10" stroke="black" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                        </g>
                        <defs>
                            <clipPath id="clip0_1513_3332">
                                <rect width="24" height="24" fill="white" />
                            </clipPath>
                        </defs>
                    </svg>
                    show more
                </div>
                <div className="blog-buttons__pagination">

                </div>
            </div>
        </div>
        </>

    )
}

export default Blog;