import React, { useState } from "react";
import "./ProductsSorting.scss"


const ProductsSorting = () => {
    const [isOpen, setIsOpen] = useState(false)

    return (
        <>
            <div className="products-sorting" onClick={() => setIsOpen(!isOpen)}>
                <div className="products-sorting__current-options">
                    <div className="products-sorting__current-options__name">
                        Price: High to Low
                    </div>
                    <svg width="18" height="11" viewBox="0 0 18 11" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17.2891 1.05948L9.28906 9.05948L1.28906 1.05948" stroke="#4F5665" strokeWidth="1.5" strokeMiterlimit="10" />
                    </svg>
                </div>
            </div>
            {isOpen &&
                <div className="products-sorting__list-options">
                    <div className="products-sorting__list-options__option">
                        
                    </div>
                </div>
            }
        </>

    )
}

export default ProductsSorting;