import React, { useState } from "react";
import { Link, NavLink } from "react-router-dom";

import "./Header.scss"

import ModalWindow from "../authorization/modal-window/ModalWindow";


function Header() {

    const [isAuthorization, setIsAuthorization] = useState(false)
    const [isSearch, setIsSearch] = useState(false)

    const isActiveSearch = (e) => {
        setIsSearch(!isSearch)
        const defaultLinks = document.querySelector(".default-links")
        const search = document.querySelector(".header__search")


        if (isSearch) {
            search.classList.remove('is-active')
            defaultLinks.classList.remove('display-none')
        }
        else {
            search.classList.add('is-active')
            defaultLinks.classList.add('display-none')

        }
        e.stopPropagation();
    }

    return (
        <header className="header">
            <nav className="nav-links">
                <div className="logo">
                    <Link to="/">
                        <svg width="24" height="21" viewBox="0 0 24 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <g clipPath="url(#clip0_1254_792)">
                                <path d="M16.6675 17.7935L15.7896 16.192L3.25585 16.1877L11.0247 2.02531L10.0155 0.5L0.54541 17.7999H2.37157L2.37188 17.7993L16.6675 17.7935Z" fill="#090909" />
                                <path d="M5.35388 15.2361L7.18004 15.2199L13.5477 3.65059L21.2654 17.5667H23.0495L13.4087 0.5L5.35388 15.2361Z" fill="#090909" />
                                <path d="M3.14221 20.4934L4.02009 18.8922L18.4675 18.8879L12.5211 7.85197L13.5304 6.32666L21.178 20.5001H19.3518L19.3515 20.4995L3.14221 20.4934Z" fill="#090909" />
                            </g>
                            <defs>
                                <clipPath id="clip0_1254_792">
                                    <rect width="22.5041" height="20" fill="white" transform="translate(0.54541 0.5)" />
                                </clipPath>
                            </defs>
                        </svg>
                        ASICXchange
                    </Link>
                </div>
                <div className="default-links">
                    <ul className="header-list-links">
                        <li className="list-link"><NavLink to="/" className={({ isActive }) =>(isActive ? " active" : "")}>Home</NavLink></li>
                        <li className="list-link"><NavLink to="/about-us">About Us</NavLink></li>
                        <li className="list-link">
                            <NavLink to="/products">
                                Products
                                <svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M1.29749 1.5L5.29749 5.5L9.29749 1.5" stroke="#706E6E" strokeWidth="1.5" strokeMiterlimit="10" />
                                </svg>
                            </NavLink>
                        </li>
                        <li className="list-link"><NavLink to="/blog">Blog</NavLink></li>
                        <li className="list-link">
                            <NavLink to="/help-center">
                                Help Center
                                <svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M1.29749 1.5L5.29749 5.5L9.29749 1.5" stroke="#706E6E" strokeWidth="1.5" strokeMiterlimit="10" />
                                </svg>
                            </NavLink>
                        </li>
                        <li className="list-link"><NavLink to="/contacts">Contacts</NavLink></li>
                    </ul>
                </div>
                <div className="header__search">
                    {/* <button className="header-bottom__search-btn" onClick={() => isActiveSearch(false)} style={{ opacity: 1 }}>
                        <svg width="17" height="17" viewBox="0 0 17 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1.45605 1.4964L15.4561 15.4964M1.45605 15.4964L15.4561 1.4964" stroke="#6858F4" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                    </button> */}
                    <input placeholder="Search..." type="text" className="header__search-input" />
                </div>
                <div className="icons-action unselectable">
                    <div className="icons-action__element" onClick={isActiveSearch}>
                        <svg width="41" height="41" viewBox="0 0 41 41" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M17.6854 25.28C21.9049 25.28 25.3254 21.8595 25.3254 17.64C25.3254 13.4205 21.9049 10 17.6854 10C13.466 10 10.0454 13.4205 10.0454 17.64C10.0454 21.8595 13.466 25.28 17.6854 25.28Z" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <path d="M31.0453 31.0001L22.9353 22.8901" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <rect x="1.04541" y="1" width="39" height="39" rx="19.5" stroke="black" />
                        </svg>
                    </div>
                    <div className="icons-action__element" >
                        <div className="icons-action__element-theme">
                            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M18.3755 15.1483C17.9018 16.1374 17.1714 16.9813 16.2606 17.592C15.3498 18.2028 14.2918 18.558 13.1969 18.6208C12.1021 18.6835 11.0104 18.4514 10.0358 17.9487C9.06115 17.446 8.23918 16.6911 7.65558 15.7626C7.07197 14.8342 6.74809 13.7662 6.71769 12.6699C6.68729 11.5737 6.95148 10.4894 7.48273 9.53001C8.01398 8.57063 8.79284 7.77131 9.73812 7.21536C10.6834 6.65941 11.7605 6.36719 12.8572 6.36915C13.4092 6.36795 13.9588 6.44254 14.4905 6.59082C13.413 6.93259 12.4932 7.64845 11.8973 8.60908C11.3015 9.56971 11.0688 10.7118 11.2414 11.8289C11.414 12.9461 11.9804 13.9647 12.8384 14.7008C13.6963 15.4369 14.7892 15.8418 15.9197 15.8425C16.7869 15.8438 17.6373 15.6034 18.3755 15.1483Z" fill="black" />
                                <rect x="1.04541" y="1" width="23" height="23" rx="11.5" stroke="black" />
                            </svg>
                            Dark
                        </div>
                    </div>
                    <div className="icons-action__element" onClick={() => setIsAuthorization(true)}>
                        <svg width="41" height="41" viewBox="0 0 41 41" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M20.5454 20.9691C23.71 20.9691 26.2754 18.518 26.2754 15.4945C26.2754 12.471 23.71 10.02 20.5454 10.02C17.3808 10.02 14.8154 12.471 14.8154 15.4945C14.8154 18.518 17.3808 20.9691 20.5454 20.9691Z" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <path d="M10.0454 31.001L10.4154 29.0424C10.8525 26.7805 12.1051 24.7362 13.9557 23.2649C15.8063 21.7935 18.1379 20.988 20.5454 20.9883C22.9558 20.9888 25.2897 21.7971 27.1407 23.2722C28.9916 24.7474 30.2425 26.7961 30.6754 29.0615L31.0454 31.0201" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <rect x="1.04541" y="1" width="39" height="39" rx="19.5" stroke="black" />
                        </svg>
                    </div>
                    <div className="icons-action__element">
                        <svg width="41" height="41" viewBox="0 0 41 41" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M25.3154 10.9502C24.3415 10.9711 23.3898 11.2448 22.5535 11.7444C21.7173 12.2441 21.0253 12.9525 20.5454 13.8002C20.0655 12.9525 19.3735 12.2441 18.5373 11.7444C17.701 11.2448 16.7493 10.9711 15.7754 10.9502C12.6054 10.9502 10.0454 13.8002 10.0454 17.3202C10.0454 23.6802 20.5454 30.0502 20.5454 30.0502C20.5454 30.0502 31.0454 23.6802 31.0454 17.3202C31.0454 13.8002 28.4854 10.9502 25.3154 10.9502Z" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <rect x="1.04541" y="1" width="39" height="39" rx="19.5" stroke="black" />
                        </svg>
                    </div>
                    <div className="icons-action__element">
                        <svg width="41" height="41" viewBox="0 0 41 41" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M11.9554 15.73H29.1354V28.14C29.1354 28.8985 28.8341 29.626 28.2977 30.1623C27.7614 30.6987 27.0339 31 26.2754 31H14.8154C14.0569 31 13.3294 30.6987 12.7931 30.1623C12.2567 29.626 11.9554 28.8985 11.9554 28.14V15.73Z" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <path d="M15.7754 18.59V14.77C15.7754 13.5049 16.278 12.2916 17.1725 11.3971C18.0671 10.5026 19.2803 10 20.5454 10C21.8105 10 23.0238 10.5026 23.9183 11.3971C24.8129 12.2916 25.3154 13.5049 25.3154 14.77V18.59" stroke="black" strokeWidth="1.5" strokeMiterlimit="10" />
                            <rect x="1.04541" y="1" width="39" height="39" rx="19.5" stroke="black" />
                        </svg>
                    </div>
                </div>
            </nav>
            {isAuthorization &&
                <ModalWindow isActive={setIsAuthorization} />
            }
        </header>
    )
}

export default Header