import React from "react";
import "./CountryFilter.scss"

const countries = [{ name:"USA" },{ name:"Canada"},{name:"Thailand"}]

const CountryFilter = () => {
    return (
        <div className="filter-box">
            <div className="filter-title">
            Available in Country
            </div>
            <div className="filter-actions">
                {countries.map(country => (
                    <div className="filter-actions__checkbox" key={country.name}>
                        <input className="filter-actions__checkbox-input" type="checkbox" id={country.name}/>
                        <label htmlFor={country.name} >{country.name}</label>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default CountryFilter;